<template>
  <div>
    <Row type="flex" justify="end" class="code-row-bg">
      <Col style="margin-right: 100px">
        <delete-comment :item="item" :textWriterId="textWriterId"></delete-comment>
        <Tooltip content="回复">
          <Button type="text" @click="flag=true">
            <Icon type="md-text" size="20"/>
          </Button>
        </Tooltip>
      </Col>
    </Row>
    <Row v-if="$store.state.userInfo!==''&&flag===true">
      <Col offset="8">
        <Input v-model="commentReply" style="width: 400px" type="textarea" :autosize="{minRows: 2,maxRows: 2}"
               placeholder="写下你的评论"></Input>
        <Button type="primary" size="large" @click="replyCommment">评论</Button>
        <Button type="text" size="large" @click="flag=false">取消</Button>
      </Col>
    </Row>
    <Row type="flex" align="middle" justify="center" v-else-if="this.flag===true">
      <Col span="4">
        <router-link to="/login">
          <Button type="primary">登陆后评论</Button>
        </router-link>
      </Col>
    </Row>
  </div>
</template>

<script>
  import deleteComment from './DeleteComment'
  export default {
    name: "reply",
    inject:['reload'],
    data() {
      return {

        flag: false,
        commentReply:''
      }
    }, methods: {
      reply() {
        this.flag=true
      },replyCommment(){
        if(this.commentReply!==''){
          var params = new URLSearchParams();
          params.append('commentTextid', this.item.commentTextid);
          params.append('commentUserid', this.$store.state.userInfo.userId);
          params.append('commentContent', this.commentReply);
          params.append('commentReplyid',this.item.commentId)
          this.axios.post('comment', params).then(res => {
            if (res.data.code === 100) {
              this.$Message.success("回复成功");
              this.reload()
            } else {
              this.$Message.error("回复失败");
            }
          }).catch(error => {
            this.$Message.warning("回复错误");
          })
        }else{
          this.$Message.error("回复内容为空")
        }
      }
    }, components: {deleteComment}, mounted() {

    }, props: ['item','textWriterId']
  }
</script>

<style scoped>

</style>



