<template>
  <div>
  <div class="box">
     <Row style="margin-top: 15px">
     <Col offset="1">
       <Breadcrumb>
         <BreadcrumbItem to="/"> <Icon type="ios-home-outline"></Icon>主页</BreadcrumbItem>
         <BreadcrumbItem :to="beforePath"  v-if="this.$router.history.current.path.indexOf('/editor')!==-1">
           <Icon type="ios-paper-outline" />文章列表</BreadcrumbItem>
         <BreadcrumbItem><Icon type="ios-bookmark-outline" />查看文章</BreadcrumbItem>
       </Breadcrumb>
     </Col>
     </Row>
    <Row type="flex">
      <Col offset="1" style="font-weight: bolder;font-size: 40px"> {{this.text.textTitle}}</Col>
    </Row>
    <Row type="flex" align="middle">
      <Col offset="1" style="font-size: 15px;">
        <Button type="text" @click="$router.push('/personHome/'+text.user.userId);reload()" >
          <img class="round_icon" src="../../../static/img/default.jpg" v-if="this.text.user.userHeadportrait==='default.jpg'"/>
          <Head-portrait :src="this.text.user.userHeadportrait"  v-else ></Head-portrait>
        </Button>
      </Col>
      <Col class="headBox">
        <p style="font-size:25px;">
          {{this.userName}}
          <span v-if="this.$store.state.userInfo===''">
            <Button type="success"   size="small" shape="circle" style="margin: 10px" @click="handleFocusOn" >+关注</Button>
          </span>
          <span v-else-if="this.text.textWriterid!==this.$store.state.userInfo.userId">
             <Button type="error"  size="small" shape="circle"   v-if="flag" @click="handleCancleFocus">取消关注</Button>
            <Button type="success"   size="small" shape="circle" style="margin: 10px" @click="handleFocusOn" v-else>+关注</Button>
          </span>
        </p>
        <Tooltip content="最后修改日期" style="margin-right: 5px">
          {{this.text.textReleasedate}}
        </Tooltip>
        <span>点击量 {{this.text.textCount}}</span>
        <span>评论数 {{this.text.textCommentnum}}</span>
        <span>点赞数 {{this.text.textLikenum}}</span>
        <span>收藏数 {{this.text.textCollectnum}}</span>
      </Col>
    </Row>
    <Row v-if="!editor">
        <Row>
          <Col offset="2">
              <p v-html="text.textContent"></p>
          </Col>
        </Row>
        <Row type="flex" justify="center" align="middle" >
          <Col>
            <Tooltip content="收藏">
            <Button type="text"  shape="circle" ghost v-if="!this.collect" @click="handleCollect"><Icon type="ios-star"  size="40" class="bt" ></Icon></Button>
            <Button type="text"  shape="circle" ghost  @click="handleCancleCollect" v-else><Icon type="ios-star"  size="40" class="btn" ></Icon></Button>
            </Tooltip>
            <Tooltip content="点赞">
            <Button type="text"   shape="circle" ghost  v-if="!this.like" @click="handleLike"><Icon type="ios-thumbs-up" size="40"  class="bt"></Icon></Button>
            <Button type="text"   shape="circle" ghost @click="handleCancleLike" v-else><Icon type="ios-thumbs-up" size="40"  class="btn"></Icon></Button>
            </Tooltip>
          </Col>
        </Row>
        <Row v-if="text.textWriterid===this.$store.state.userInfo.userId">
        <Col type="flex" justify="center" align="middle"  >
          <Button type="success" size="large" @click="editor=true">编辑</Button>
        </Col>
       </Row>
    </Row>
    <Row v-else>
      <Editor :textId="this.text.textId"></Editor>
      <Col offset="20">
      <Button type="info" @click="editor=false">退出编辑</Button>
      </Col>
    </Row>

  </div>
    <div class="box">
    <Comment :textId="textId" :textWriterId="text.textWriterid" ></Comment>
    </div>
  </div>
</template>

<script>
  import HeadPortrait from './HeadPortrait'
import Comment from './Comment'
import Editor from '../personal/Editor'
  export default {
    name: "ReadText",
    inject: ['reload'],
    data() {
      return {
        flag:false,
        collect:false,
        like:false,
        text: '',
        userName: '',
        textId:0,
        editor:false,
        beforePath:'/personHome/'
      }
    }, methods: {
      getText() {
        this.axios.get('text/' + this.textId).then(res => {
          if (res.data.code === 100) {
            this.text = res.data.map.texts
            this.userName = this.text.user.userName
             this.checkFocusOr()
            this.checkCollect()
            this.checkLike()
          } else {
            this.$router.push('/index')
          }
        }).catch(error => {
        })
      }, checkFocusOr() {
        this.flag=false
        if(this.$store.state.userInfo!==''){
          this.axios.get('fansCheck', {
            params: {
              focusUserid: this.$store.state.userInfo.userId,
              focusBeuserid: this.text.textWriterid
            }
          })
            .then(res => {
              if (res.data.code === 100) {
                //console.log("你是他的粉丝")
                this.flag = true
              } else {
                //console.log("你还没有关注他")
              }
            }).catch(error => {
            this.$Message.warning("检查是否关注出错")
          })
        }
      }, handleCancleFocus(){
        //取消关注，应该是自己的关注列表去掉这个
        //console.log(this.row.focusBeuserid+"    "+this.row.focusUserid)
        if(this.$store.state.userInfo!=='') {
          this.axios.delete('focus', {
            params: {
              focusUserid: this.$store.state.userInfo.userId,
              focusBeuserid: this.text.textWriterid
            }
          }).then(response => {
            if (response.data.code === 100) {
              this.$Message.success("取消关注")
              this.$store.commit('setFocusNumSub')
              this.reload()
            } else {
              this.$Message.error("取关失败")
            }
          }).catch(error => {
            this.$Message.warning("取消关注失败")
          })
        }else{
          this.$router.push("/login")
        }
      },handleFocusOn(){
        if(this.$store.state.userInfo===''){
          this.$Message.info("还未登陆--->正在前往登陆")
          this.$router.push('/login')
        }else{
          this.axios.get('focus',{params:{focusUserid:this.$store.state.userInfo.userId,focusBeuserid: this.text.textWriterid}})
            .then(res=>{
              //console.log(res.data.code);
              if(res.data.code===100){
                this.$Message.success("关注成功")
                this.$store.commit('setFocusNumAdd')
                this.reload()
              }else{
                this.$Message.error("关注失败")
              }
            }).catch(error=>{
            this.$Message.warning("关注失败")
          })
        }

      },
        checkCollect(){
        this.collect=false
        if(this.$store.state.userInfo!==''){
          //console.log(this.$store.state.userInfo.userId+"       "+this.text.textId)
          this.axios.get('collectCheck',{params:{collectUserid:this.$store.state.userInfo.userId,collectTextid:this.text.textId}}).
            then(res=>{
              if(res.data.code===100){
                this.collect=true
              }
          }).catch(error=>{
            this.$Message.warning("获取是否收藏失败")
          })
        }
        },handleCollect(){
        if(this.$store.state.userInfo!==''){
          this.axios.get('collect',{params:{collectUserid:this.$store.state.userInfo.userId,collectTextid:this.text.textId}}).
          then(res=>{
            if(res.data.code===100){
              this.collect=true
              this.$Message.success("首场成功")
              this.$store.commit('setCollectionsNumAdd')
              this.reload()
            }else{
              this.$Message.error("收藏失败")
            }
          }).catch(error=>{
            this.$Message.warning("收藏失败")
          })
        }else{
          this.$router.push('/login')
        }
      },handleCancleCollect(){
        if(this.$store.state.userInfo!==''){
          this.axios.delete('collect',{params:{collectUserid:this.$store.state.userInfo.userId,collectTextid:this.text.textId}}).
          then(res=>{
            if(res.data.code===100){
              this.collect=false
              this.$Message.success("取消收藏成功")
              this.$store.commit('setCollectionsNumSub')
              this.reload()
            }else{
              this.$Message.error("取消收藏失败")
            }
          }).catch(error=>{
            this.$Message.warning("取消收藏失败")
          })
        }else{
          this.$router.push('/login')
        }
      },checkLike(){
        this.like=false
        if(this.$store.state.userInfo!==''){
          //console.log(this.$store.state.userInfo.userId+"       "+this.text.textId)
          this.axios.get('likeCheck',{params:{likesUserid:this.$store.state.userInfo.userId,likesTextid:this.text.textId}}).
          then(res=>{
            if(res.data.code===100){
              this.like=true
            }
          }).catch(error=>{
            this.$Message.warning("获取是否点赞失败")
          })
        }},handleLike(){
          if(this.$store.state.userInfo!==''){
            this.axios.get('like',{params:{likesUserid:this.$store.state.userInfo.userId,likesTextid:this.text.textId}}).
            then(res=>{
              if(res.data.code===100){
                this.like=true
                this.$Message.success("点赞成功")
                this.reload()
              }else{
                this.$Message.error("点赞失败")
              }
            }).catch(error=>{
              this.$Message.warning("点赞失败")
            })
          }else{
            this.$router.push('/login')
          }
        },handleCancleLike(){
          if(this.$store.state.userInfo!==''){
            this.axios.delete('like',{params:{likesUserid:this.$store.state.userInfo.userId,likesTextid:this.text.textId}}).
            then(res=>{
              if(res.data.code===100){
                this.like=false
                this.$Message.success("取消点赞成功")
                this.reload()
              }else{
                this.$Message.error("取消点赞失败")
              }
            }).catch(error=>{
              this.$Message.warning("取消点赞失败")
            })
          }else{
            this.$router.push('/login')
          }
        }

    }, components: {Comment,Editor,HeadPortrait}, created() {
      this.textId = this.$route.query.textId
      this.beforePath+=this.$route.params.userId+"/textsList"
      this.getText()
    }
  }
</script>

<style scoped>
  .box {
    padding: 5px;
    border: 1px solid #cccccc;
    box-shadow: 0 0 5px #ccc;
    border-radius: 5px;
    width: 1000px;
    margin: 0px auto;
    margin-top: 20px;
  }

  .round_icon:hover {
    transform: scale(1.4);
  }

  .round_icon {
    width: 50px;
    height: 50px;
    display: flex;
    border-radius: 50%;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    transition: all 0.6s;
  }
  .headBox {
    margin: 10px;
    padding: 10px;
  }
  .bt{
    color:gray;
  }
  .btn{
    color:deeppink;
  }
  .btn:hover{
    color:gray;
  }
  .bt:hover{
    color:deeppink;
  }
</style>



