<template>
  <Tooltip content="删除" v-if="item.commentUserid===this.$store.state.userInfo.userId||textWriterId===this.$store.state.userInfo.userId">
      <Col>
        <Button type="text" @click="deleteComment" ><Icon type="ios-trash" size="20" /></Button>
      </Col>
  </Tooltip>
</template>

<script>
    export default {
        name: "DeleteComment",
        inject:['reload'],
        data() {
            return {
            }
        }, methods: {
          deleteComment(){
            this.axios.delete('comment',{params:{
                commentId:this.item.commentId,
                commentTextid:this.item.commentTextid,
                commentUserid:this.item.commentUserid
              }}).then(res=>{
                if(res.data.code===100){
                  this.$Message.success("删除成功")
                  this.reload()
                }else{
                  this.$Message.warning("删除失败")
                }
            }).catch(error=>{
              this.$Message.warning("删除失败")
            })
          }
      }, components: {}, mounted() {
        },props:['item','textWriterId']
    }
</script>

<style scoped>

</style>



