<template>
  <div >
    <Row style="margin-bottom: 30px">
      <Col offset="1"><h2>评论区</h2></Col>

    <Row type="flex" align="bottom" v-if="this.$store.state.userInfo!==''">
      <Col offset="1">
        <Button type="text"  @click="$router.push('/personHome/'+$store.state.userInfo.userId);reload()" v-if="this.$store.state.userInfo!==''&&this.$store.state.userInfo.userHeadportrait==='default.jpg'">
        <img src="../../../static/img/default.jpg" alt="" class="round_icon"/>
        </Button>
        <Button type="text" @click="$router.push('/personHome/'+$store.state.userInfo.userId);reload()" v-else>
        <Head-portrait :src="this.$store.state.userInfo.userHeadportrait" size="large" ></Head-portrait>
        </Button>
      </Col>
      <Col offset="1">
        <Input v-model="comment" style="width: 600px" type="textarea" :autosize="{minRows: 3,maxRows: 3}"
               placeholder="写下你的评论"></Input>
      </Col>
      <Col offset="1">
        <Button type="primary" size="large" @click="commentText">评论</Button>
      </Col>
    </Row>
    <Row type="flex" align="middle" justify="center" v-else>
      <Col span="4">
        <router-link to="/login">
          <Button type="primary" long>登陆后评论</Button>
        </router-link>
      </Col>
    </Row>
    </Row>
    <Row v-for="(items,index) in commentArray" :key="items.commentId" class="comment"><!--渲染一级评论-->
      <Row v-if="items.commentReplyid===null">
        <Col offset="1" span="1" style="margin-right: 40px">
          <Button type="text"  @click="$router.push('/personHome/'+items.user.userId);reload()">
          <Avatar src="../../../static/img/default.jpg"  size="large"  v-if="items.user.userHeadportrait==='default.jpg'"/>
          <Head-img :src="items.user.userHeadportrait"   v-else></Head-img>
          </Button>
        </Col>
        <Col style="margin-left: 90px">
          <p style="font-size:17px;">{{items.user.userName}}</p>
          <span>{{commentArray.length-index}}楼</span>
          <span>{{items.commentDate}}</span>
        </Col>
        <Row>
          <Col offset="2">
            <p class="content">
              {{items.commentContent}}</p>
          </Col>
        </Row>
        <div class="commentHr"></div>
        <reply :item="items" :textWriterId="textWriterId"></reply>
              <Row v-for="(item,index) in commentArray" :key="item.commentId" >
                <Row v-if="item.commentReplyid!==null&&item.commentReplyid===items.commentId" style="margin-left:40px" >
                  <Col offset="1" span="1" style="margin-right: 40px">
                    <Button  type="text"  @click="$router.push('/personHome/'+item.user.userId);reload()" >
                    <Avatar src="../../../static/img/default.jpg" size="large"
                            v-if="item.user.userHeadportrait==='default.jpg'"/>
                    <Head-img :src="item.user.userHeadportrait"  v-else></Head-img>
                    </Button>
                  </Col>
                  <Col style="margin-left: 90px">
                    <p style="font-size:17px;">{{item.user.userName}}</p>
                    <span>{{commentArray.length-index}}楼</span>
                    <span>{{item.commentDate}}</span>
                  </Col>
                  <Row>
                    <Col offset="2">
                      <div class="content">
                        {{item.commentContent}}
                      </div>
                      <Tooltip content="删除" v-if="item.commentUserid===$store.state.userInfo.userId||textWriterId===$store.state.userInfo.userId" >
                      <Button type="text" @click="deleteComment(item)" ><Icon type="ios-trash" size="20" /></Button>
                     </Tooltip>
                    </Col>
                  </Row>

                  <div class="commentHrReply"></div>
                </Row>
              </Row>
      </Row>
    </Row>
    <!--<Row type="flex" justify="end" class="code-row-bg">
      <Col span="4" offset="4">
        总共{{total}}条数据
      </Col>
      <Col>
      <Page :total="total" :current="pageNum" show-elevator @on-change="pageChange"/>
      </Col>
    </Row>-->
  </div>
</template>

<script>
  import reply from './Reply'
  import HeadPortrait from './HeadPortrait'
  import HeadImg from './HeadImg'
  export default {
    name: "Comment",
    inject: ['reload'],
    data() {
      return {
        commentArray: [],
        comment: '',
        flag: false,
        // pages: 0,
        // pageSize: 10,
        // pageNum: 1,
        // total: 0,
      }
    }, methods: {
      getComment() {
        this.axios.get('comment/text/' + this.textId).then(res => {
          if (res.data.code == 100) {
            this.commentArray = res.data.map.TextComment
             this.commentArray.reverse()
            // this.pages = res.data.map.TextComment.pages
            // this.pageNum = res.data.map.TextComment.pageNum
            // this.pageSize = res.data.map.TextComment.pageSize
            // this.total = res.data.map.TextComment.total
          }
        }).catch(error => {
          this.$Message.warning("获取评论失败")
        })
      }, commentText() {
        if(this.comment!==''){
          var params = new URLSearchParams();
          params.append('commentTextid', this.textId);
          params.append('commentUserid', this.$store.state.userInfo.userId);
          params.append('commentContent', this.comment);
          this.axios.post('comment', params).then(res => {
            if (res.data.code === 100) {
              this.$Message.success("评论成功");
              this.reload()
            } else {
              this.$Message.error("评论失败");
            }
          }).catch(error => {
            this.$Message.warning("评论错误");
          })
        }else{
          this.$Message.error("评论内容为空")
        }

      }, pageChange(page) {
        this.pageNum = page
      },deleteComment(item){
        this.axios.delete('comment',{params:{
            commentId:item.commentId,
            commentTextid:item.commentTextid,
            commentUserid:item.commentUserid
          }}).then(res=>{
          if(res.data.code===100){
            this.$Message.success("删除成功")
            this.reload()
          }else{
            this.$Message.warning("删除失败")
          }
        }).catch(error=>{
          this.$Message.warning("删除失败")
        })
      }
    }, components: {reply,HeadPortrait,HeadImg}, mounted() {
      this.getComment()
    }, props: ['textId','textWriterId'],
    watch:{
      pageNum:function () {
        this.getComment()
      }
    }
  }
</script>

<style scoped>
  .box {
    padding: 5px;
    border: 1px solid #cccccc;
    box-shadow: 0 0 5px #ccc;
    border-radius: 5px;
    width: 1000px;
    margin: 0px auto;
    margin-top: 20px;
  }

  .round_icon:hover {
    transform: scale(1.4);
  }

  .round_icon {
    width: 50px;
    height: 50px;
    display: flex;
    border-radius: 50%;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    transition: all 0.6s;
  }

  .comment {
    margin: 10px;
    padding: 10px;
  }

  .commentHr {
    margin: 10px;
    margin-left: 40px;
    width: 800px;
    border-bottom: 1px rgba(114, 116, 99, 0.3) solid;
  }
  .commentHrReply {
    margin: 10px;
    margin-left: 40px;
    width: 760px;
    border-bottom: 1px rgba(114, 116, 99, 0.3) solid;
  }

  .content {
    font-size: 17px;
    margin: 5px;
    width: 650px;
    display: flex;
    float: left;
  }


</style>



