<template>
  <div class="edit_container" >
    <Form ref="formCustom" :model="formCustom" :rules="ruleValidate" style="text-align: center" >
      <FormItem prop="title" style="width: 200px;margin:0px auto;">
       <Input type="text" v-model="formCustom.title" placeholder="请输入标题" ></Input>
      </FormItem>
      <FormItem>
      <p style="text-align: center;font-weight: bolder;font-size: 20px;margin: 5px">作者：{{formCustom.writer}}</p>
      </FormItem>
      <FormItem  >
    <quill-editor
      v-model="formCustom.content"
      ref="myQuillEditor"
     >
    </quill-editor>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="handleSubmit('formCustom')">修改</Button>
        <Button @click="handleReset" style="margin-left: 8px">清空</Button>
      </FormItem>
    </Form>
  </div>
</template>
<script>
  import {quillEditor} from "vue-quill-editor"; //调用编辑器
  //import quillConfig from '../../quill/quill-config.js'
  //import * as Quill from 'quill'  //引入编辑器
  //quill图片可拖拽改变大小
 // import ImageResize from 'quill-image-resize-module'
  //Quill.register('modules/imageResize', ImageResize)

  export default {
    name:'Editor',
    inject: ['reload'],
    components: {
      quillEditor
    },
    data() {
      return {
        formCustom:{
          content: ``,
          textId:0,
          str:'',
          title:'',
          writer:'',
        },
        ruleValidate: {
          title: [
            { required: true, message: '标题不为空', trigger: 'blur' }
          ]
        }
      }
    },
    methods: {
      getContent(){
        this.axios.get('text/'+this.formCustom.textId).then(res=>{
          if(res.data.code===100){
            if(res.data.map.texts.textWriterid===this.$store.state.userInfo.userId){
              this.formCustom.content=res.data.map.texts.textContent
              this.formCustom.title=res.data.map.texts.textTitle
              this.formCustom.writer=res.data.map.texts.user.userName
            }else{
              this.$router.push("/index")
            }
          }else{
            this.$Message.error("获取文章失败")
          }
        }).catch(error=>{
          this.$Message.warning("获取文章失败")
        })
      }, handleSubmit (name) {
        this.$refs[name].validate((valid) => {
          if(this.formCustom.content==''){
            this.$Message.error("内容不能为空")
          }
          else if (valid) {
            var params = new URLSearchParams();
            params.append('textContent', this.formCustom.content);
            params.append('textId', this.formCustom.textId);
            params.append('textTitle', this.formCustom.title);
            this.axios.put('text/'+this.formCustom.textId,params).then(res=>{
              if(res.data.code===100){
                this.$Message.success('更新成功');
                this.reload()
              }else{
                this.$Message.error("更新失败")
              }
            }).catch(error=>{
              this.$Message.warning("更新文章失败")
            })
          } else {
            this.$Message.error('Fail!');
          }
        })
      },
      handleReset () {
        this.formCustom.content='',
          this.formCustom.title=''
      }
      },
    computed: {
      editor() {
        return this.$refs.myQuillEditor.quill;
      },
    },mounted(){
      this.formCustom.textId=this.textId
      this.getContent()  // 请求后台返回的内容字符串
      //this.str = this.escapeStringHTML(content);
    },props:['textId']
  }
</script>



